import React from "react";
import Breadcrumbs from "../../Views/Breadcrumbs";
import InformationViews from '../../Views/InformationViews';

const CelenderPurchasePage = ({
    context
}) => {
    const breadcrumbs = context.breadcrumbs;

    return (
        <React.Fragment>
            <Breadcrumbs breadcrumbs={breadcrumbs} />
            <InformationViews.PaymentsConteiner>
                {
                    context?.page_info?.content && <span dangerouslySetInnerHTML={{ __html: context.page_info.content }} ></span>
                }
            </InformationViews.PaymentsConteiner>
        </React.Fragment>
    )
}


export default CelenderPurchasePage;